//https://codesandbox.io/s/how-to-build-material-ui-navbar-header-cu3sf?fontsize=14&hidenavigation=1&theme=dark
import React, { Component } from 'react';
import { HashRouter, Router, Route, Link, } from "react-router-dom";


import Home from "./components/MainPage";
import Header from "./components/HeaderNavbar";

import PublicRoute from './Utils/PublicRoute';
//import PrivateRoute from './Utils/PrivateRoute';
//import Login from "./Login";
//          <PublicRoute path="/Login" component={Login} />
//https://reactrouter.com/web/example/url-params

import QRReader from "./components/QRRead";
import AssetDistribution from "./components/AssetDistribution/AssetDistribution";
import InfrastructureTransaction from "./components/DataTransaction/InfrastructureTransaction";
import LandstructureTransaction from "./components/DataTransaction/LandstructureTransaction";
import TreeManagementTransaction from "./components/DataTransaction/TreeManagementTransaction";
import FleetTransaction from "./components/DataTransaction/FleetTransaction";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {objectid: 'test'};  
  }
  render() {
    return (
      <HashRouter basename="/">
        <div>
          <Header />          
        </div>
        <div>
          <Route exact path="/" component={Home} />
          <Route path="/QRReader" component={QRReader} />
          <Route path="/AssetDistribution/:objid/" component={AssetDistribution} />
          <Route path="/AssetDistribution" component={AssetDistribution} />
          <Route path="/InfrastructureTransaction" component={InfrastructureTransaction} />
          <Route path="/LandstructureTransaction" component={LandstructureTransaction} />
          <Route path="/TreeManagementTransaction" component={TreeManagementTransaction} />
          <Route path="/FleetTransaction" component={FleetTransaction} />
        </div>
      </HashRouter>
    );
  }
}

export default App;